<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process141"></a>

<p><br></p>
<p><span class="rvts17">章节简介</span></p>
<p class="rvps2"><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1081.jpg" display="block"><span class="rvts18"></span>
<br><span class="rvts19">随着剧情的进展，跟随着马拉迪登上复仇女神号。本章内容较短，但非常重要，将影响接下来章节的内容。</span>
<br><span class="rvts21">虽然登上了复仇女神号，但是由于该船是使用特殊方法控制启动，所以需要探索该船的上中下三个层面。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



